<template>
  <v-app>
    <v-toolbar dense>
      <v-toolbar-title>
        Time {{ chronometer === '0' ? '' : chronometer }}
      </v-toolbar-title>

      <v-spacer></v-spacer>
      <v-btn @click="onRevertRoll" icon>
        <v-icon>mdi-restore</v-icon>
      </v-btn>
      <PlayerModal />
      <SettingsModal />
    </v-toolbar>

    <Tables />

    <v-card class="main-container elevation-5" dark flat tile>
      <v-snackbar v-model="snackbar" :color="snackbarColor" top>
        {{ snackbarText }}
      </v-snackbar>

      <div v-if="currentPlayer" class="d-flex justify-space-around mt-8 mb-5">
        <v-btn
            v-for="(item, index) in players.slice().reverse()"
            :key="item.id"
            :class="playerClass(index)"
            :color="item.color"
            @click="onRoll(item)"
            fab
        >
          <span class="display-1">{{ item.name[0] }}</span>
        </v-btn>
      </div>

      <Dice :rollDice="onRoll" />

      <v-list style="max-height: calc(100vh - 525px); overflow-y: hidden" class="mt-10 ml-5 mr-5">
        <transition-group name="list-complete" tag="div">
          <v-list-item
              v-for="item in diceLog.slice(1, players.length + 1)"
              :key="item.timestamp"
              class="list-complete-item headline">
            <v-list-item-avatar class="ml-10">
              <v-avatar :color="item.player.color" size="40">
                <span>{{ item.player.name[0] }}</span>
              </v-avatar>
            </v-list-item-avatar>
            <v-list-item-content>
              <div class="d-flex justify-space-around align-content-center">
                <div class="grey--text headline" v-if="twelveSidedDieEnabled">{{ item.dice[2] }}</div>
                <div class="grey--text display-1">{{ item.dice[0] + item.dice[1] }}</div>
              </div>
            </v-list-item-content>
          </v-list-item>
        </transition-group>
      </v-list>
    </v-card>
  </v-app>
</template>

<script>
import { mapActions, mapMutations, mapGetters } from 'vuex';

import Dice          from './components/Dice.vue';
import Tables        from './components/Tables.vue';
import PlayerModal   from './components/PlayerModal.vue';
import SettingsModal from './components/SettingsModal.vue';

export default {
  name: 'App',
  components: {
    Dice,
    Tables,
    PlayerModal,
    SettingsModal,
  },
  data: () => ({
    snackbar: false,
    snackbarText: '',
    snackbarColor: 'success',
    movingPlayerId: '',
  }),
  computed: {
    ...mapGetters({
      players: 'players',
      colors: 'colors',
      diceLog: 'diceLog',
      startedAt: 'startedAt',
      chronometer: 'chronometer',
      twelveSidedDieEnabled: 'twelveSidedDieEnabled',
    }),
    currentPlayer() {
      return this.players[0];
    },
  },
  mounted() {
    window.clock = setInterval(function() {
      this.updateChronometer();
    }.bind(this), 10000);

    document.addEventListener('keydown', event => {
      if (event.key === 'Control') {
        this.$set(this, 'movingPlayerId', this.currentPlayer.id);
      }
    });

    document.addEventListener('keyup', event => {
      if (event.key === 'Control') {
        this.$set(this, 'movingPlayerId', '');
      } else if (event.code === 'Space') {
        this.onRoll();
      } else if (event.code === 'Backspace') {
        this.onRevertRoll();
      } else if (!event.ctrlKey && event.code === 'ArrowLeft') {
        this.playerBack();
      } else if (!event.ctrlKey && event.code === 'ArrowRight') {
        this.playerNext();
      } else if (event.ctrlKey && (event.code === 'ArrowLeft' || event.code === 'ArrowRight')) {
        this.onUpdatePlayers(event.code);
      }
    });
  },
  methods: {
    ...mapActions({
      rollDice:   'rollDice',
      revertRoll: 'revertRoll',
    }),
    ...mapMutations({
      updateChronometer: 'updateChronometer',
      playerNext:        'playerNext',
      playerBack:        'playerBack',
      updatePlayers:     'updatePlayers',
    }),
    playerClass(index) {
      const players = this.players.slice().reverse();
      if (players[index].id === this.movingPlayerId) {
        return 'mx-auto moving-player';
      }
      if (index === players.length -1 && !this.movingPlayerId) {
        return 'mx-auto active-player';
      } else {
        return 'mx-auto filter-opacity';
      }
    },
    onUpdatePlayers(code) {
      if (this.movingPlayerId) {
        const players = this.players.slice();
        const index = players.findIndex(p => p.id === this.movingPlayerId);

        let sort = code === 'ArrowLeft' ? index + 1 : index - 1;
        if (sort < 0) {
          sort = players.length -1;
        } else if (sort > players.length -1) {
          sort = 0;
        }

        this.movingPlayer = players.splice(index, 1)[0];

        players.splice(sort, 0, this.movingPlayer);
        this.updatePlayers(players);
      }
    },
    onRoll(player) {
      this.rollDice(player);
    },
    onRevertRoll() {
      if (this.diceLog.length) {
        this.revertRoll();
      }
    },
  },
};
</script>

<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

/* LIST ANIMATION START */
.list-complete-item {
  transition: all 2s;
}
.list-complete-enter, .list-complete-leave-to
  /* .list-complete-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(-70px);
}
.list-complete-leave-active {
  position: absolute;
}
/* LIST ANIMATION END */

.v-application--wrap {
  background: #1E1E1E;
}

html, body {
  overflow-y: hidden !important;
}

.main-container {
  height: 100%;
}

.active-player {
  border: 3px solid white !important;
}

.moving-player {
  border: 3px solid #00ff00 !important;
}

.filter-opacity {
  filter: opacity(0.75);
}

@media screen and (min-width: 1050px) {
  .main-container {
    margin: auto;
    width: 950px;
  }
}
</style>
