<template>
  <div>
    <v-btn @click="dialog = true" icon>
      <v-icon>mdi-account-group</v-icon>
    </v-btn>

    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-snackbar v-model="snackbar" :color="snackbarColor" top>
          {{ snackbarText }}
        </v-snackbar>

        <v-card-title>
          <span class="headline mx-auto">Players</span>
        </v-card-title>

        <template v-if="players.length">
          <v-list-item v-for="item in players" :key="item.id">
            <div class="mr-5">
              <v-btn @click="onUpdatePlayer(item)" :color="item.color" class="mx-auto" fab small>
                <span class="headline">{{ item.name[0] }}</span>
              </v-btn>
            </div>
            <v-list-item-content>
              <v-list-item-title v-text="item.name"></v-list-item-title>
            </v-list-item-content>
            <v-btn @click="onDeletePlayer(item)" text icon color="white">
              <v-icon>mdi-delete</v-icon>
            </v-btn>
          </v-list-item>
        </template>

        <v-card-title>
          <span class="headline mx-auto">New player</span>
        </v-card-title>

        <v-card-text>
          <v-container>
            <form v-on:submit.prevent @submit="onAddPlayer">
              <v-text-field v-model="name" label="Name" required></v-text-field>
              <v-row>
                <v-col v-for="color in colors" :key="color" sm="3" xs="4">
                  <div :style="selectedColor === color ? { height: '56px', width: '56px', border: 'solid white 4px', 'border-radius': '100%' } : {}">
                    <v-avatar @click="selectedColor = color" :color="color"></v-avatar>
                  </div>
                </v-col>
              </v-row>
            </form>
            <v-card-actions>
              <v-btn class="mx-auto mt-5" color="primary" @click="onAddPlayer">Add player</v-btn>
            </v-card-actions>
            <v-card-actions>
              <v-btn class="ml-auto" color="grey.darken" @click="dialog = false">Close</v-btn>
            </v-card-actions>
          </v-container>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex';

export default {
  name: 'PlayerModal',
  data: () => ({
    dialog: false,
    snackbar: false,
    snackbarColor: '',
    snackbarText: '',
    name: '',
    selectedColor: '',
  }),
  computed: {
    ...mapGetters({
      players: 'players',
      colors: 'colors',
    }),
  },
  methods: {
    ...mapMutations({
      addPlayer: 'addPlayer',
      deletePlayer: 'deletePlayer',
      updatePlayer: 'updatePlayer',
    }),
    onAddPlayer() {
      const name = this.name && (this.name[0].toUpperCase() + this.name.slice(1, 100));
      const color = this.selectedColor;

      let error = '';

      if (!name)
        error += ' - name';
      if (!color)
        error += ' - color';

      if (this.players.find(p => p.name === name))
        error += ' - Name already taken';

      if (error) {
        this.snackbar = true;
        this.snackbarText = 'Error ' + error;
        this.snackbarColor = 'error';
        return;
      }

      this.addPlayer({ name, color });

      this.name = '';
      this.selectedColor = '';
    },
    onUpdatePlayer(player) {
      const color = this.selectedColor;
      if (color) {
        player = Object.assign({}, player);
        player.color = color;
        this.updatePlayer(player);
      }
      this.selectedColor = '';
    },
    onDeletePlayer(player) {
      this.deletePlayer(player);
    },
  }
};
</script>
